import { Box, Table, TableBody, TableCell, TableHead, TableRow } from "@rp/react/web/mui/core"
import { csvData } from "./watchlistExample"

export const WatchlistUploadExampleTable = () => {
  return (
    <Box
      sx={theme => ({
        my: 4,
        border: `1px solid ${theme.palette.outline.border}`,
        borderRadius: "5px",
        "& table": {
          width: 350,
          "& th, & td": {
            p: 1,
            fontSize: 14,
            lineHeight: "20px",
            borderBottom: `1px solid ${theme.palette.outline.border}`,
          },
          "& th": { color: theme.palette.textColor[700], fontWeight: 700, fontSize: 12 },
          "& th:nth-of-type(2), & td:nth-of-type(2)": {
            textAlign: "right",
          },
          "& tr:last-of-type td": {
            borderBottom: "none",
          },
        },
      })}
    >
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>NAME</TableCell>
            <TableCell>LISTING</TableCell>
            <TableCell>TICKER</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {csvData.map(item => {
            return (
              <TableRow key={item.ticker}>
                <TableCell>{item.name}</TableCell>
                <TableCell>{item.listing}</TableCell>
                <TableCell>{item.ticker}</TableCell>
              </TableRow>
            )
          })}
        </TableBody>
      </Table>
    </Box>
  )
}
